.dropbtn {
    background-color: rgb(32, 97, 104);
    color: black;
    border: 3px solid #ede88d;
    width: 180px;
    padding: 16px;
    font-size: 22px;
    border-radius: 12px;
    align-items: center;
    cursor: pointer;
}

.dropdown {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgb(239, 239, 199);
    margin-top: 15%;
}

.dropdown-content {
    display: none;
    background-color: rgb(61, 125, 132);
    width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(220, 225, 150, 0.2);
    z-index: 1;
}

.dropdown-content a {
    color: rgb(15, 15, 15);
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    
}

.dropdown-content a:hover {
    background-color: rgb(32, 86, 92);
}

/* .dropdown:hover .dropdown-content {
    display: block;
} */

.dropdown:hover .dropbtn {
    background-color: rgb(35, 74, 77);
}